<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">焦点TAB管理</span>
        <el-button
          type="success"
          @click="openAddDialog()"
        >新增</el-button>
      </el-card>
    </el-row>
    <el-table
      border
      :data="tableData"
    >
      <el-table-column
        prop="index"
        label="序号"
        align="center"
        width="80"
      >
      </el-table-column>
      <el-table-column
        prop="word"
        label="热词"
        align="center"
      >
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        width="230"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.$index, scope.row)"
          >编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="新增"
      :visible.sync="dialogVisible"
      width="30%"
      :append-to-body='true'
      :before-close="handleClose"
      :closeOnClickModal='false'
    >
      <div class="el-dialog-news">
        <el-form
          label-position="top"
          :rules="rules"
          ref="formLabelAlign"
          label-width="80px"
          :model="formLabelAlign"
        >
          <el-form-item
            label="热词"
            prop="word"
          >
            <el-input v-model="formLabelAlign.word"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleClose">取 消</el-button>
        <el-button
          type="primary"
          @click="addInfo('formLabelAlign')"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { authToken, authName } from "@/config";
import { storageHelper, getCurTime, utcTimeChange } from "@/utils";
export default {
  data() {
    return {
      //表格
      tableData: [],
      //弹出框
      dialogVisible: false,
      //表单
      formLabelAlign: {
        word: "",
      },
      rules: {
        word: [{ required: true, message: "请输入热词", trigger: "blur" }],
      },
    };
  },
  mounted() {
    this.getInfoList();
  },
  methods: {
    handleClose() {
      var that = this;
      this.$confirm('是否保存？',{
        showClose:false
      })
      .then(() => {
        that.addInfo('formLabelAlign');
      })
      .catch(() => {this.dialogVisible = false});
    },
    openAddDialog() {
      this.formLabelAlign = {
        word: "",
      };
      this.dialogVisible = true;
    },
    //编辑
    handleEdit(index, row) {
      this.dialogVisible = true;
      this.formLabelAlign = {
        id: row.id,
        word: row.word,
      };
    },
    //删除
    handleDelete(index, row) {
      var that = this;
      this.$confirm("是否确认删除该条信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        var params = { id: row.id };
        that.submitInfo("content/hotword/delete", params, "删除成功!");
      }).catch(() => {});
    },
    //编辑框提交按钮
    addInfo(formType) {
      var that = this;
      this.$refs[formType].validate(valid => {
        if (valid) {
          var method = "";
          var info = "";
          if (that.formLabelAlign.hasOwnProperty("id")) {
            method = "content/hotword/update";
            info = "修改成功!";
          } else {
            method = "content/hotword/create";
            info = "添加成功!";
          }
          that.submitInfo(method, that.formLabelAlign, info);
        } else {
          return false;
        }
      });
    },
    getInfoList() {
      var params = {};
      var that = this;
      axios.get("content/hotword/list", { params }).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            var array = [];
            var json = response.data;
            console.log(json);
            for (var i = 0; i < json.length; i++) {
              json[i].index = i + 1;
              array.push(json[i]);
            }
            that.tableData = array;
          } else {
            //console.log(code);
          }
        },
        function(error) {
          //console.log(error);
        }
      );
    },
    submitInfo(method, params, info) {
      var that = this;
      axios.post(method, params).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            that.$message({
              type: "success",
              message: info
            });
            that.dialogVisible = false;
            that.getInfoList();
          } else {
            that.$message({
              type: "success",
              message: "操作失败:" + response.message
            });
          }
        },
        function(error) {}
      );
    },
  }
};
</script>

<style scoped>
.content-title {
  margin-right: 20px;
}
.advanced-box {
  padding: 20px 10px;
  margin-top: 15px;
  background-color: #f7f7f7;
}
.pager {
  float: right;
  padding: 10px 0;
}
.el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.el-dialog__footer {
  border-top: 1px solid #ccc;
}
.el-form-item__label {
  padding-bottom: 0 !important;
}
</style>


